    DOWNLOAD PAGE
  //////////////////////////////////////////////////
*/
.download_box {
  border: 1px solid #ED225D;
  padding: 0.4em;
  margin: 1em 1.75em 0 0;
  width: 18.65em;
  color: #333 !important;
  height:7.45em;
  position: relative;
}

.download_box:hover {
  border: 1px solid #ED225D;
  background: #ED225D;
  color: #FFF !important;
}

.download_box.half_box {
  width:8.45em;
  float: left;
}

.download_box h4 {
  font-size: 1em;
  margin: 0;
  padding-bottom: 0.3em;
  border-bottom: 0.09em dashed;
  border-bottom-color: #ED225D;
}

.download_box:hover h4 {
  -webkit-text-stroke-width: 0;
  border-bottom-color: #FFF;
}

.download_box p {
  font-size: 0.65em;
  margin: 0;
  position:absolute;
  bottom:1em;
}

.download_box svg {
  height:0.65em;
  width:0.65em;
  position: absolute;
  bottom: 3.5em;
}

.download_box:hover svg {
  fill: white;
}

.download_box h4 + p {display: block;}

#download-page .link_group {
  width: 100%;
  margin-bottom: 3em;
}

#download-page p {
  margin-bottom: 0;
}

